/* eslint-disable jsx-a11y/tabindex-no-positive */
import { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';

// material-ui
import { useMediaQuery, Typography, Accordion, AccordionDetails, AccordionSummary } from '@mui/material';
import { ExpandMore as ExpandMoreIcon } from '@mui/icons-material';
import { useTheme } from '@mui/material/styles';

// project imports
import MainCard from 'ui-component/cards/MainCard';
import { Languages } from './Languages';
import productFn from './component/productFn'; //functions
import EditStep from './component/EditStep';
import PhotoGallery from './component/PhotoGallery';
import ProductSummary from './component/ProductSummary';
import ProductAttribute from './component/ProductAttribute';
import ProductService from './component/ProductService';
import ProductRTS from './component/ProductRTS';
import ProductDescription from './component/ProductDescription';
import Confirmation from './component/Confirmation';

// ==============================|| SINGLE PAGE APPLICATION ||============================== //
let productModified = {};

const App = () => {
    //
    console.log('productEdit app running');
    let localization = useSelector((state) => state.localization); //
    let globalization = useSelector((state) => state.globalization); //
    let [langPack, setLangPack] = useState(Languages[localization.locale]); //currentLanguage
    let [loginData] = useState(globalization.baseData.loginData);
    let navigate = useNavigate(); //react-router-dom
    //. // & 
    let [activeStep, setActiveStep] = useState(0);
    let [expanded, setExpanded] = useState({ panel1: true, panel2: false, panel3: false, panel4: false });
    let photoCols = useMediaQuery(useTheme().breakpoints.down('md')) ? 3 : 6;
    //
    let urlParams = new URLSearchParams(location.pathname.replace('/product/', ''));
    let [productInfo, setProductInfo] = useState({}); //API
    let [xmlSchema, setXmlSchema] = useState(null);
    let [rtsProduct, setRtsProduct] = useState(urlParams.get('product_type') == 1); //RTS
    let [group_id] = useState(Number(urlParams.get('group_id'))); //id
    let [cat_id, setCat_id] = useState(Number(urlParams.get('category_id'))); //id
    let [real_product_id] = useState(Number(urlParams.get('real_product_id'))); //id
    let [product_id] = useState(urlParams.get('product_id')); //id()API
    let [editMode] = useState(urlParams.get('product_id') ? 'update' : 'add'); // ?   

    useEffect(() => {
        setLangPack(Languages[localization.locale]);
        return; //
    }, [localization.locale]);

    useEffect(() => {
        console.log('I want product editing');
        if (!loginData) return navigate('/');
        if (!editMode) return queryResult(false, langPack.steps.productIdError);
        if (editMode === 'add') {
            makeProductInfo({ cat_id }); // 
        } else {
            makeProductExample({ product_id: real_product_id }); //   &  
            getProductInfo({ product_id }); //()
        }
        return () => {
            console.log('I leave product edit');
            productModified = {};
        }; // & 
    }, []);

    //___________________________________________________________
    async function getProductInfo(obj) {
        let paramObj = ma.productDetailQuery(obj);
        let msg = await fetchAPI(paramObj);
        if (!msg || !msg.response.status) return queryResult(false, langPack.common.netFail + '\n' + msg ? msg.response.result : '');
        let res = ma.productDetailCheck(msg);
        let { status, result } = res.response;
        if (!status || !result) return queryResult(status, result);
        //console.log(result);
        if (result.category_id) setCat_id(Number(result.category_id));
        if (result.rts) setRtsProduct(true);
        setProductInfo(result);
    } //

    async function uploadPhotos(photoFile) {
        let currentFolder = -1;
        let paramObj = ma.photoUploadQuery({ file_name: photoFile.name, image_bytes: photoFile, group_id: currentFolder });
        let msg = await fetchAPI(paramObj);
        if (!msg.response.status) return queryResult(false, langPack.common.yes);
        let res = ma.photoUploadCheck(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(false, result);
        queryResult(true, langPack.steps.uploadSuccess);
        return result;
    }

    async function makeProductInfo(obj) {
        let paramObj = ma.productSchemaQuery({
            param_product_top_publish_request: JSON.stringify({
                ...obj,
                language: localization.locale === 'enUS' ? 'en_US' : 'zh'
            })
        });
        let msg = await fetchAPI(paramObj);
        if (!msg || !msg.response.status) return queryResult(false, langPack.common.netFail + '\n' + msg ? msg.response.result : '');
        let res = ma.productSchemaCheck(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(status, result);
        let schemaResult = productFn.schemaMaker(result);
        if (!schemaResult) return queryResult(false, langPack.steps.uneditable);
        setXmlSchema(schemaResult);
    } // 
    //
    async function makeProductExample(obj) {
        let paramObj = ma.productSchemaRenderQuery({
            param_product_top_publish_request: JSON.stringify({
                ...obj,
                language: localization.locale === 'enUS' ? 'en_US' : 'zh'
            })
        });
        let msg = await fetchAPI(paramObj);
        if (!msg || !msg.response.status) return queryResult(false, langPack.common.netFail + '\n' + msg ? msg.response.result : '');
        let res = ma.productSchemaRenderCheck(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(status, result);
        let schemaResult = productFn.schemaMaker(result);
        if (!schemaResult) return queryResult(false, langPack.steps.uneditable);
        setXmlSchema(schemaResult);
    } //   &  

    const onModified = (key, value) => {
        console.log('modified', key, value);
        console.log(value.xml);
        productModified[key] = value;
    }; //

    async function modificationSubmission() {
        //console.log(productModified);
        let xmlString = productFn.schemaChecker(productModified);
        if (!xmlString) return queryResult(false, langPack.steps.modificationNull);
        let res = await productFn.schemaSubmit(xmlString, editMode, real_product_id, cat_id, localization.locale);
        if (!res || !res.response.status) return queryResult(false, langPack.common.netFail + '\n' + res ? res.response.result : '');
        queryResult(true, editMode === 'add' ? langPack.steps.addSuccess : langPack.steps.updateSuccess);
    }

    const handleAccordionChange = (panel) => (event, isExpanded) => {
        setExpanded((prev) => {
            return { ...prev, [panel]: isExpanded };
        });
    }; //
    //____________Functions return JSX_______________________________________________

    return (
        <MainCard title="Product Edit --">
            <EditStep langPack={langPack} activeStep={activeStep} setActiveStep={setActiveStep} />
            {activeStep === 0 ? (
                <>
                    <PhotoGallery
                        productFn={productFn}
                        xmlSchema={xmlSchema}
                        productModified={productModified}
                        localization={localization}
                        photoCols={photoCols}
                        rowHeight={200}
                        uploadPhotos={uploadPhotos}
                        onModified={onModified}
                        langPack={langPack}
                    />
                    <Accordion expanded={expanded['panel1']} onChange={handleAccordionChange('panel1')}>
                        <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                            <Typography variant="h2" sx={{ width: '33%', flexShrink: 0, color: 'coral' }}>
                                {langPack.form.KeyInformation}
                            </Typography>
                            <Typography sx={{ color: 'text.secondary' }}>{langPack.steps.panel1}</Typography>
                        </AccordionSummary>
                        <AccordionDetails sx={{ border: '1px groove' }}>
                            <>
                                <ProductSummary
                                    productFn={productFn}
                                    xmlSchema={xmlSchema}
                                    productModified={productModified}
                                    group_id={group_id}
                                    langPack={langPack}
                                    rtsProduct={rtsProduct}
                                    onModified={onModified}
                                />
                            </>
                        </AccordionDetails>
                    </Accordion>
                    <Accordion expanded={expanded['panel2']} onChange={handleAccordionChange('panel2')}>
                        <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                            <Typography variant="h2" sx={{ width: '33%', flexShrink: 0, color: 'red' }}>
                                {langPack.form.ProductAttributes}
                            </Typography>
                            <Typography sx={{ color: 'text.secondary' }}>{langPack.steps.panel2}</Typography>
                        </AccordionSummary>
                        <AccordionDetails sx={{ border: '1px groove' }}>
                            <>
                                <ProductAttribute
                                    productFn={productFn}
                                    xmlSchema={xmlSchema}
                                    productModified={productModified}
                                    langPack={langPack}
                                    onModified={onModified}
                                />
                            </>
                        </AccordionDetails>
                    </Accordion>
                    <Accordion expanded={expanded['panel3']} onChange={handleAccordionChange('panel3')}>
                        <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                            <Typography variant="h2" sx={{ width: '33%', flexShrink: 0, color: 'orangered' }}>
                                {langPack.form.productService}
                            </Typography>
                            <Typography sx={{ color: 'text.secondary' }}>{langPack.steps.panel3}</Typography>
                        </AccordionSummary>
                        <AccordionDetails sx={{ border: '1px groove' }}>
                            <>
                                <ProductService
                                    productFn={productFn}
                                    xmlSchema={xmlSchema}
                                    productModified={productModified}
                                    langPack={langPack}
                                    onModified={onModified}
                                />
                            </>
                        </AccordionDetails>
                    </Accordion>
                    <Accordion
                        expanded={expanded['panel4']}
                        onChange={handleAccordionChange('panel4')}
                        sx={{ display: rtsProduct ? 'block' : 'none' }}
                    >
                        <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                            <Typography variant="h2" sx={{ width: '33%', flexShrink: 0, color: 'orangered' }}>
                                {langPack.form.rtsProduct}
                            </Typography>
                            <Typography sx={{ color: 'text.secondary' }}>{langPack.steps.panel4}</Typography>
                        </AccordionSummary>
                        <AccordionDetails sx={{ border: '1px groove' }}>
                            {rtsProduct ? (
                                <>
                                    <ProductRTS
                                        productFn={productFn}
                                        xmlSchema={xmlSchema}
                                        productModified={productModified}
                                        langPack={langPack}
                                        onModified={onModified}
                                    />
                                </>
                            ) : (
                                <></>
                            )}
                        </AccordionDetails>
                    </Accordion>
                    <br />
                </>
            ) : activeStep === 1 ? (
                <>
                    <ProductDescription
                        productFn={productFn}
                        xmlSchema={xmlSchema}
                        productModified={productModified}
                        localization={localization}
                        onModified={onModified}
                        langPack={langPack}
                        productInfo={productInfo}
                    />
                </>
            ) : activeStep === 2 ? (
                <>
                    <Confirmation
                        langPack={langPack}
                        productModified={productModified}
                        showDetails={true}
                        modificationSubmission={modificationSubmission}
                    />
                </>
            ) : (
                <></>
            )}
        </MainCard>
    );
};

export default App;
