import { useState, useEffect } from 'react';
import PropTypes from 'prop-types';

// material-ui
import {
    Typography,
    Autocomplete,
    Button,
    Checkbox,
    InputAdornment,
    MenuItem,
    TextField,
    Divider,
    Tooltip,
    Chip,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper
} from '@mui/material';
import {
    Add as AddIcon,
    Remove as RemoveIcon,
    Check as CheckIcon,
    Cancel as CancelIcon,
    ModeEdit as EditIcon,
    Clear as ClearIcon,
    DeleteOutline as DeleteIcon
} from '@mui/icons-material';

let productGroupSaver; //
let paymentMethodSaver; //
let logisticsSaver; //
let priceSaver; //
let periodSaver; //
let productTypeSaver; //
function App({ xmlSchema, productFn, langPack, group_id, rtsProduct, productModified, onModified }) {
    console.log('summary running');
    //
    let [productGroupObj, setProductGroupObj] = useState({});
    //
    let [productTypeObj, setProductTypeObj] = useState({});
    //
    let [subjectObj, setSubjectObj] = useState({});
    //
    let [keywordObj, setKeywordObj] = useState({});
    //
    let [paymentMethodObj, setPaymentMethodObj] = useState({});
    //
    let [priceUnitObj, setPriceUnitObj] = useState({});
    //
    let [MOQObj, setMOQObj] = useState({});
    //
    let [priceObj, setPriceObj] = useState({});
    let [priceEdit, setPriceEdit] = useState(false);
    //
    let [ladderPeriodObj, setLadderPeriodObj] = useState({});
    let [periodEdit, setPeriodEdit] = useState(false);
    //
    let [portObj, setPortObj] = useState({});
    //
    let [logisticsModeObj, setLogisticsObj] = useState({});
    //
    let [packagingDescriptionObj, setPackagingDescriptionObj] = useState({});
    //
    let [supplyAbilityObj, setSupplyAbilityObj] = useState({});
    let {
        productGroupObjMaker,
        productGroupObjChecker,
        productTypeObjMaker,
        productTypeObjChecker,
        subjectObjMaker,
        subjectObjChecker,
        keywordObjMaker,
        keywordObjChecker,
        MOQObjMaker,
        MOQObjChecker,
        paymentMethodObjMaker,
        paymentMethodObjChecker,
        priceUnitObjMaker,
        priceUnitObjChecker,
        portObjMaker,
        portObjChecker,
        logisticsObjMaker,
        logisticsObjChecker,
        packaingDescriptionObjMaker,
        packaingDescriptionObjChecker,
        priceObjMaker,
        priceObjChecker,
        ladderPeriodObjMaker,
        ladderPeriodObjChecker,
        supplyAbilityObjMaker,
        supplyAbilityObjChecker
    } = productFn;

    useEffect(() => {
        if (!xmlSchema) return;
        let {
            productGroup,
            market,
            productTitle,
            productKeywords,
            minOrderQuantity,
            paymentMethod,
            priceUnit,
            ladderPrice,
            ladderPeriod,
            port,
            logisticsMode,
            wholeSalePkgDesc,
            supply
        } = productModified;
        productGroup ? setProductGroupObj(productGroup) : getProductGroupObj();
        market ? setProductTypeObj(market) : getProductTypeObj();
        productTitle ? setSubjectObj(productTitle) : getSubjectObj();
        productKeywords ? setKeywordObj(productKeywords) : getKeywordObj();
        minOrderQuantity ? setMOQObj(minOrderQuantity) : getMOQObj();
        paymentMethod ? setPaymentMethodObj(paymentMethod) : getPaymentMethodObj();
        priceUnit ? setPriceUnitObj(priceUnit) : getPriceUnitObj();
        ladderPrice ? setPriceObj(ladderPrice) : getPriceObj();
        ladderPeriod ? setLadderPeriodObj(ladderPeriod) : getLadderPeriodObj();
        port ? setPortObj(port) : getPortObj();
        logisticsMode ? setLogisticsObj(logisticsMode) : getLogisticsObj();
        wholeSalePkgDesc ? setPackagingDescriptionObj(wholeSalePkgDesc) : getPackaingDescriptionObj();
        supply ? setSupplyAbilityObj(supply) : getSupplyAbilityObj();
        return;
    }, [xmlSchema]);

    function getProductGroupObj() {
        let fieldObj = productGroupObjMaker();
        if (group_id) {
            productGroupSaver = fieldObj;
            return onProductGroupChange(group_id);
        }
        return setProductGroupObj(fieldObj);
    } //

    function onProductGroupChange(value) {
        if (!value) return queryResult(false, langPack.steps.productSubjectNeeded); //
        let newObj = productGroupObj._values ? { ...productGroupObj } : productGroupSaver;
        newObj = productGroupObjChecker(newObj, value);
        let setter = setProductGroupObj;
        setter(newObj); //JSX
        return onModified('productGroup', newObj); //
    } //

    function getProductTypeObj() {
        let fieldObj = productTypeObjMaker();
        if (!fieldObj) return;
        if (!fieldObj.values) {
            productTypeSaver = fieldObj;
            return onProductTypeChange(rtsProduct ? '1' : '2'); //
        }
        return setProductTypeObj(fieldObj);
    } //

    function onProductTypeChange(value) {
        if (!value || !value.trim()) return queryResult(false, langPack.steps.productSubjectNeeded); //
        let newObj = productTypeObj._values ? productTypeObj : productTypeSaver;
        newObj = productTypeObjChecker(newObj, value);
        let setter = setProductTypeObj;
        setter(newObj); //JSX
        return onModified('market', newObj); //
    } //

    function getSubjectObj() {
        return setSubjectObj(subjectObjMaker());
    } //

    function onSubjectChange(value) {
        if (!value || !value.trim()) return queryResult(false, langPack.steps.productSubjectNeeded); //
        let newObj = subjectObjChecker(subjectObj, value);
        let setter = setSubjectObj;
        setter(newObj); //JSX
        return onModified('productTitle', newObj); //
    } //

    function getKeywordObj() {
        return setKeywordObj(keywordObjMaker());
    } //

    function onKeywordChange(value) {
        if (!value || !value.trim()) return queryResult(false, langPack.steps.productKeywordNeeded); //
        let newArr = value.split(',');
        if (newArr.length > 3) {
            newArr.length = 3;
            queryResult(false, langPack.form.keywords);
        }
        let newObj = keywordObjChecker(keywordObj, newArr);
        let setter = setKeywordObj;
        setter(newObj); //JSX
        return onModified('productKeywords', newObj); //
    } //

    function getMOQObj() {
        return setMOQObj(MOQObjMaker());
    } //

    function onMOQChanged(value) {
        if (!value) return; //
        let newObj = MOQObjChecker(MOQObj, value);
        let setter = setMOQObj;
        setter(newObj); //JSX
        return onModified('minOrderQuantity', newObj); //
    } //

    function getPaymentMethodObj() {
        return setPaymentMethodObj(paymentMethodObjMaker());
    } //

    function onPaymentMethodChange(newValue) {
        paymentMethodSaver = newValue;
    } //

    function onPaymentMethodConfirm() {
        if (!paymentMethodSaver) return;
        if (!paymentMethodSaver.length) return queryResult(false, langPack.steps.productPaymentMethodsNeeded);
        let newObj = paymentMethodObjChecker(paymentMethodObj, paymentMethodSaver);
        let setter = setPaymentMethodObj;
        setter(newObj); //JSX
        paymentMethodSaver = '';
        return onModified('paymentMethod', newObj);
    } //

    function getPriceUnitObj() {
        return setPriceUnitObj(priceUnitObjMaker);
    } //

    function onPriceUnitChange(valueObj) {
        if (!valueObj) return queryResult(false, langPack.steps.productPriceUnitNeeded); //
        let newObj = priceUnitObjChecker(priceUnitObj, valueObj);
        let setter = setPriceUnitObj;
        setter(newObj); //JSX
        return onModified('priceUnit', newObj); //
    } //

    function getPortObj() {
        return setPortObj(portObjMaker());
    } //

    function onPortChange(value) {
        if (!value || !value.trim()) return queryResult(false, langPack.steps.productPortNeeded); //
        let newObj = portObjChecker(portObj, value);
        let setter = setPortObj;
        setter(newObj); //JSX
        return onModified('port', newObj); //
    } //

    function getLogisticsObj() {
        return setLogisticsObj(logisticsObjMaker());
    } //

    function onLogisticsChange(newValue) {
        logisticsSaver = newValue;
    } //

    function onLogisticsConfirm() {
        if (!logisticsSaver) return;
        if (!logisticsSaver.length) return queryResult(false, langPack.steps.productLogisticsModeNeeded);
        let newObj = logisticsObjChecker(logisticsModeObj, logisticsSaver);
        let setter = setLogisticsObj;
        setter(newObj); //JSX
        logisticsSaver = '';
        return onModified('logisticsMode', newObj);
    } //

    function getPackaingDescriptionObj() {
        return setPackagingDescriptionObj(packaingDescriptionObjMaker());
    } //

    function onPackagingDescriptionChange(value) {
        if (!value || !value.trim()) return queryResult(false, langPack.steps.productPackagingDescriptionNeeded); //
        let newObj = packaingDescriptionObjChecker(packagingDescriptionObj, value);
        let setter = setPackagingDescriptionObj;
        setter(newObj); //JSX
        return onModified('wholeSalePkgDesc', newObj); //
    } //

    function getPriceObj() {
        return setPriceObj(priceObjMaker());
    } //

    const onPriceEditMode = () => {
        if (priceEdit) {
            onPriceChange('edit');
            //
        } else {
            //
            let priceArr = priceObj._values;
            let lastPrice = priceArr[priceArr.length - 1];
            priceSaver = { ...lastPrice };
        }
        setPriceEdit((prev) => !prev);
        return;
    }; //

    const onPriceSave = (key, value, index) => {
        if (!priceSaver) priceSaver = {};
        priceSaver[key] = Number(value);
        if (priceSaver.quantity && priceSaver.price) onPriceChange('edit', index);
        return;
    }; //

    const onPriceChange = (mode, index) => {
        let newObj = { ...priceObj };
        let priceArr = newObj._values;
        if (mode === 'add') {
            let lastPrice = priceArr[priceArr.length - 1];
            if (!lastPrice.price || priceArr.length >= 4) return; //
            priceArr = [...priceArr, { quantity: '', price: '' }]; //
        } else if (mode === 'edit') {
            if (!priceSaver) return;
            if (!priceSaver.price || !priceSaver.quantity) return queryResult(false, langPack.steps.nullNotAllowed);
            if (priceArr.length !== 1) {
                if (priceSaver.quantity <= priceArr[priceArr.length - 2].quantity) return queryResult(false, langPack.steps.MOQError);
                if (priceSaver.price >= priceArr[priceArr.length - 2].price) return queryResult(false, langPack.steps.PriceError);
            }
            //MOQ & 
            priceArr.pop();
            priceArr = [...priceArr, priceSaver]; //
            priceSaver = '';
        } else if (mode === 'remove') {
            if (priceArr.length <= 1) return;
            priceArr.splice(index, 1); //
        }
        newObj._values = priceArr;
        let setter = setPriceObj;
        if (mode === 'add') return setter(newObj); //JSX; 
        priceArr = priceArr.filter((obj) => obj.price);
        if (!priceArr.length) return; //
        newObj = priceObjChecker(newObj, priceArr);
        setter(newObj); //JSX
        onMOQChanged(priceArr[0].quantity); //MOQ
        setPriceEdit((prev) => !prev); //
        return onModified('ladderPrice', newObj);
    }; // & 

    function getLadderPeriodObj() {
        return setLadderPeriodObj(ladderPeriodObjMaker());
    } //

    const onPeriodEditMode = () => {
        if (periodEdit) {
            onPeriodChange('edit');
            //
        } else {
            //
            let newArr = ladderPeriodObj._values;
            let lastObj = newArr[newArr.length - 1];
            periodSaver = { ...lastObj };
        }
        setPeriodEdit((prev) => !prev);
        return;
    }; //

    const onPeriodSave = (key, value, index) => {
        if (!periodSaver) periodSaver = {};
        periodSaver[key] = Number(value);
        if (periodSaver.quantity && periodSaver.day) onPeriodChange('edit', index);
        return;
    }; //

    const onPeriodChange = (mode, index) => {
        let newObj = { ...ladderPeriodObj };
        let newArr = newObj._values;
        if (mode === 'add') {
            let lastObj = newArr[newArr.length - 1];
            if (!lastObj.day || newArr.length >= 3) return; //
            newArr = [...newArr, { quantity: '', day: '' }]; //
        } else if (mode === 'edit') {
            if (!periodSaver) return;
            if (!periodSaver.day || !periodSaver.quantity) return queryResult(false, langPack.steps.nullNotAllowed);
            newArr.pop();
            newArr = [...newArr, periodSaver]; //
            periodSaver = '';
        } else if (mode === 'remove') {
            if (newArr.length <= 1) return;
            newArr.splice(index, 1); //
        }
        newObj._values = newArr;
        let setter = setLadderPeriodObj;
        if (mode === 'add') return setter(newObj); //JSX; 
        newArr = newArr.filter((obj) => obj.day);
        if (!newArr.length) return; //
        newObj = ladderPeriodObjChecker(newObj, newArr);
        setter(newObj); //JSX
        if (!rtsProduct && priceUnitObj._values) {
            let { quantity, day } = newArr[newArr.length - 1];
            let supply_number = Number(quantity) / Number(day);
            let time_unit = 'Day';
            let quantity_unit = priceUnitObj._values.value;
            onSupplyAbilityChange({ supply_number, time_unit, quantity_unit }); //MOQ
        }
        setPeriodEdit((prev) => !prev); //
        return onModified('ladderPeriod', newObj);
    }; // & 

    function getSupplyAbilityObj() {
        return setSupplyAbilityObj(supplyAbilityObjMaker());
    } //

    function onSupplyAbilityChange(valueObj) {
        if (!valueObj) return queryResult(false, langPack.steps.productSupplyAbilityNeeded); //
        let newObj = supplyAbilityObjChecker(supplyAbilityObj, valueObj);
        let setter = setSupplyAbilityObj;
        setter(newObj); //JSX
        return onModified('supply', newObj); //
    } //

    return (
        <>
            <Tooltip title={langPack.form.subject}>
                <TextField
                    margin="normal"
                    fullWidth
                    key={JSON.stringify(subjectObj._values)}
                    id="productTitle"
                    variant="outlined"
                    label={<Typography variant="h3">{subjectObj.name}</Typography>}
                    defaultValue={subjectObj._values ? subjectObj._values.value : ''}
                    onBlur={(e) => onSubjectChange(e.target.value)}
                />
            </Tooltip>
            <Tooltip title={langPack.form.keywords}>
                <TextField
                    margin="normal"
                    fullWidth
                    key={JSON.stringify(keywordObj._values)}
                    id="keywords"
                    variant="outlined"
                    label={<Typography variant="h3">{keywordObj.name}</Typography>}
                    defaultValue={keywordObj._values ? keywordObj._values.value : ''}
                    onBlur={(e) => onKeywordChange(e.target.value)}
                />
            </Tooltip>
            <Autocomplete
                id="add-payment-method"
                key={JSON.stringify(paymentMethodObj._values)}
                sx={{ display: 'inline' }}
                multiple
                limitTags={1}
                disableCloseOnSelect={true}
                onBlur={onPaymentMethodConfirm}
                onChange={(e, value) => onPaymentMethodChange(value)}
                defaultValue={paymentMethodObj._values}
                options={paymentMethodObj._options || []}
                getOptionLabel={(option) => option.key}
                isOptionEqualToValue={(option, value) => option.value === value.value}
                renderTags={(options, getTagProps) =>
                    options.map((option, index) => <Chip variant="outlined" label={option.value} {...getTagProps({ index })} />)
                }
                renderOption={(props, option, { selected }) => (
                    <li {...props}>
                        <Checkbox style={{ marginRight: 8 }} checked={selected} />
                        {option.value}
                    </li>
                )}
                renderInput={(params) => (
                    <TextField
                        {...params}
                        fullWidth={false}
                        sx={{ marginTop: '13px', minWidth: '220px' }}
                        label={<Typography variant="h3">{paymentMethodObj.name}</Typography>}
                        placeholder={langPack.form.selectOrInput}
                    />
                )}
            />
            <Autocomplete
                id="add-price-unit"
                key={priceUnitObj._values ? JSON.stringify(priceUnitObj._values.value) : ''}
                sx={{ display: 'inline' }}
                onChange={(e, value) => onPriceUnitChange(value)}
                defaultValue={priceUnitObj._values}
                options={priceUnitObj._options || []}
                getOptionLabel={(option) => option.key}
                isOptionEqualToValue={(option, value) => option.value === value.value}
                renderOption={(props, option, { selected }) => (
                    <li {...props}>
                        <Checkbox style={{ marginRight: 8 }} checked={selected} />
                        {option.key}
                    </li>
                )}
                renderInput={(params) => (
                    <TextField
                        {...params}
                        fullWidth={false}
                        sx={{ marginTop: '13px', minWidth: '220px' }}
                        label={<Typography variant="h3">{priceUnitObj.name}</Typography>}
                        placeholder={langPack.form.selectOrInput}
                    />
                )}
            />
            <Tooltip title={langPack.form.min_order_quantity}>
                <TextField
                    type="number"
                    sx={{ maxWidth: '280px', display: 'none' }}
                    margin="normal"
                    key={JSON.stringify(priceObj._values)}
                    id="min_order_quantity"
                    variant="outlined"
                    label={<Typography variant="h3">{MOQObj.name}</Typography>}
                    value={priceObj._values ? priceObj._values[0].quantity : MOQObj._values ? MOQObj._values.value : ''}
                    InputProps={{
                        endAdornment: (
                            <InputAdornment position="end">
                                {priceUnitObj._values ? priceUnitObj._values.key : langPack.form.pieces}
                            </InputAdornment>
                        )
                    }}
                    onBlur={(e) => onMOQChanged(e.target.value)}
                />
            </Tooltip>
            <br />
            <Autocomplete
                id="change-logistics-mode"
                key={JSON.stringify(logisticsModeObj._values)}
                multiple
                disableCloseOnSelect={true}
                sx={{ display: 'inline' }}
                onBlur={onLogisticsConfirm}
                onChange={(e, value) => onLogisticsChange(value)}
                defaultValue={logisticsModeObj._values ? logisticsModeObj._values.map((obj) => obj.key) : []}
                options={logisticsModeObj._options ? logisticsModeObj._options.map((obj) => obj.key) : []}
                renderTags={(options, getTagProps) =>
                    options.map((option, index) => <Chip variant="outlined" label={option} {...getTagProps({ index })} />)
                }
                renderOption={(props, option, { selected }) => (
                    <li {...props}>
                        <Checkbox style={{ marginRight: 8 }} checked={selected} />
                        {option}
                    </li>
                )}
                renderInput={(params) => (
                    <TextField
                        {...params}
                        fullWidth={false}
                        sx={{ marginTop: '13px', minWidth: '220px' }}
                        label={<Typography variant="h3">{logisticsModeObj.name}</Typography>}
                        placeholder={langPack.form.selectOrInput}
                    />
                )}
            />
            <Tooltip title={langPack.form.subject}>
                <TextField
                    margin="normal"
                    key={JSON.stringify(portObj._values)}
                    id="port"
                    variant="outlined"
                    label={<Typography variant="h3">{portObj.name}</Typography>}
                    defaultValue={portObj._values ? portObj._values.value : ''}
                    onBlur={(e) => onPortChange(e.target.value)}
                />
            </Tooltip>
            <Tooltip title={langPack.form.subject}>
                <TextField
                    margin="normal"
                    key={JSON.stringify(packagingDescriptionObj._values)}
                    id="packaging-description"
                    variant="outlined"
                    label={<Typography variant="h3">{packagingDescriptionObj.name}</Typography>}
                    defaultValue={packagingDescriptionObj._values ? packagingDescriptionObj._values.value : ''}
                    onBlur={(e) => onPackagingDescriptionChange(e.target.value)}
                />
            </Tooltip>
            <hr />
            <Table size="small">
                <TableHead>
                    <TableRow>
                        <TableCell>{langPack.table.min_order_quantity}</TableCell>
                        <TableCell>{langPack.table.price}</TableCell>
                        <TableCell>
                            <Button startIcon={priceEdit ? <CheckIcon /> : <EditIcon />} onClick={onPriceEditMode}>
                                {priceEdit ? langPack.steps.editDone : langPack.steps.editPrice}
                            </Button>
                        </TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {priceObj && priceObj._values ? (
                        priceObj._values.map((obj, index) => (
                            <TableRow key={index} sx={{ '&:last-child td, &:last-child th': { border: 0 } }} hover={true}>
                                <TableCell>
                                    <div
                                        style={{
                                            display: 'flex',
                                            flexDirection: 'row',
                                            alignItems: 'center'
                                        }}
                                    >
                                        <Typography sx={{ display: 'inline' }}></Typography>
                                        {priceEdit && index === priceObj._values.length - 1 ? (
                                            <TextField
                                                size="small"
                                                variant="outlined"
                                                type="number"
                                                defaultValue={obj.quantity}
                                                onBlur={(e) => onPriceSave('quantity', e.target.value, index)}
                                            />
                                        ) : (
                                            obj.quantity
                                        )}
                                        <Typography sx={{ display: 'inline' }}>
                                            {`&nbsp;`.decodeHTMLEntity()} {priceUnitObj._values ? priceUnitObj._values.key : ''}
                                        </Typography>
                                    </div>
                                </TableCell>
                                <TableCell component="th" scope="row">
                                    {priceEdit && index === priceObj._values.length - 1 ? (
                                        <TextField
                                            size="small"
                                            variant="outlined"
                                            type="number"
                                            defaultValue={obj.price}
                                            onBlur={(e) => onPriceSave('price', e.target.value, index)}
                                        />
                                    ) : (
                                        obj.price
                                    )}
                                </TableCell>
                                <TableCell>
                                    {index === 0 ? (
                                        <Button startIcon={<AddIcon />} onClick={() => onPriceChange('add', index)}>
                                            {langPack.steps.add}
                                        </Button>
                                    ) : (
                                        <Button startIcon={<RemoveIcon />} onClick={() => onPriceChange('remove', index)}>
                                            {langPack.steps.remove}
                                        </Button>
                                    )}
                                </TableCell>
                            </TableRow>
                        ))
                    ) : (
                        <></>
                    )}
                </TableBody>
            </Table>
            <hr />
            <Table size="small">
                <TableHead>
                    <TableRow>
                        <TableCell>{langPack.table.min_order_quantity}</TableCell>
                        <TableCell>{langPack.table.day}</TableCell>
                        <TableCell>
                            <Button startIcon={periodEdit ? <CheckIcon /> : <EditIcon />} onClick={onPeriodEditMode}>
                                {periodEdit ? langPack.steps.editDone : langPack.steps.editPeriod}
                            </Button>
                        </TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {ladderPeriodObj && ladderPeriodObj._values ? (
                        ladderPeriodObj._values.map((obj, index) => (
                            <TableRow key={index} sx={{ '&:last-child td, &:last-child th': { border: 0 } }} hover={true}>
                                <TableCell>
                                    <div
                                        style={{
                                            display: 'flex',
                                            flexDirection: 'row',
                                            alignItems: 'center'
                                        }}
                                    >
                                        <Typography sx={{ display: 'inline' }}></Typography>
                                        {periodEdit && index === ladderPeriodObj._values.length - 1 ? (
                                            <TextField
                                                size="small"
                                                variant="outlined"
                                                type="number"
                                                defaultValue={obj.quantity}
                                                onBlur={(e) => onPeriodSave('quantity', e.target.value, index)}
                                            />
                                        ) : (
                                            obj.quantity
                                        )}
                                        <Typography sx={{ display: 'inline' }}>
                                            {`&nbsp;`.decodeHTMLEntity()} {priceUnitObj._values ? priceUnitObj._values.key : ''}
                                        </Typography>
                                    </div>
                                </TableCell>
                                <TableCell component="th" scope="row">
                                    {periodEdit && index === ladderPeriodObj._values.length - 1 ? (
                                        <TextField
                                            size="small"
                                            variant="outlined"
                                            type="number"
                                            defaultValue={obj.day}
                                            onBlur={(e) => onPeriodSave('day', e.target.value, index)}
                                        />
                                    ) : (
                                        obj.day
                                    )}
                                </TableCell>
                                <TableCell>
                                    {index === 0 ? (
                                        <Button startIcon={<AddIcon />} onClick={() => onPeriodChange('add', index)}>
                                            {langPack.steps.add}
                                        </Button>
                                    ) : (
                                        <Button startIcon={<RemoveIcon />} onClick={() => onPeriodChange('remove', index)}>
                                            {langPack.steps.remove}
                                        </Button>
                                    )}
                                </TableCell>
                            </TableRow>
                        ))
                    ) : (
                        <></>
                    )}
                </TableBody>
            </Table>
        </>
    );
}

App.propTypes = {
    xmlSchema: PropTypes.object,
    productFn: PropTypes.object,
    langPack: PropTypes.object,
    group_id: PropTypes.number,
    rtsProduct: PropTypes.bool,
    productModified: PropTypes.object,
    onModified: PropTypes.func
};

export default App;
